  <div>
    <p class="question">Q: What can I expect from my first session?</p>
    <p>
      A: The first one to three sessions are “getting to know you” sessions. Prior to your first appointment,
      you will be asked to review and sign informed consent forms and complete online intake paperwork via a
      secure client portal. When you present for your first session, we will discuss your current symptoms,
      psychiatric history, background, and treatment goals. This discussion will help us develop a plan to
      best meet your needs.
    </p>
    <p class="question">Q: Do you accept any insurance plans?</p>
    <p>
      A: I am an out-of-network provider and thus do not bill directly to any insurance plans. Session fees
      are collected at the conclusion of clients' appointments. Payment is accepted via check, cash, or credit
      card. Concerns about the cost of psychotherapy can serve as a barrier to those who are in need of help.
      I specialize in evidence-based psychotherapy, which has been extensively studied and shown to result in
      significant, enduring improvement in clients' symptoms and lives. With evidence-based psychotherapy, you
      may need fewer sessions to feel better and achieve the positive outcomes you desire.
    </p>
    <p class="question">Q: How can I get my insurance to reimburse the cost of my sessions?</p>
    <p>
      A: I provide all clients with superbills that they can submit to their insurance company for
      out-of-network reimbursement of their sessions. Insurance plan benefits vary widely, but if you have
      out-of-network benefits you may be able to get part or all of the cost of your sessions reimbursed
      directly to you.
    </p>
    <p class="question">Q: Do you ever meet with clients for therapy via video (i.e., Skype or FaceTime)?</p>
    <p>
      A: I offer video therapy to clients located throughout the state of Maryland. I use a secure,
      HIPAA-compliant telehealth platform known as VSee. This software is free to clients and can be easily
      downloaded and installed on your computer. Please note that insurance plans vary with regard to
      reimbursement of psychotherapy delivered via telehealth software; therefore, you should clarify your
      insurance plan benefits if you wish to submit for out-of-network reimbursement.
    </p>
